/** @jsx jsx */
import { jsx, Styled } from "theme-ui";

import { graphql, Link } from "gatsby";
import { mapCraft } from "./mappings";

import { useEffect, useState } from "react";
import InstantSearch from "@components/InstantSearch";

const InsightsFor = (props) => {
  const { category } = mapCraft(props.data.craft);

  const { title } = category;

  useEffect(() => {
    if (title) {
      console.log(title);
      setRefinements({
        ...defaultRefinements,
        industries: [title],
      });
    }
  }, []);

  const [defaultRefinements, setRefinements] = useState({
    type: [],
    industries: [],
  });

  return (
    <div>
      <InstantSearch
        refinementType="industries"
        defaultRefinement={defaultRefinements.industries}
        isSearch={false}
        heading={title}
      />
    </div>
  );
};

export default InsightsFor;

export const query = graphql`
  query IndustriesQuery($id: Craft_QueryArgument) {
    craft {
      category(id: [$id]) {
        ... on Craft_industries_Category {
          title
        }
      }
    }
  }
`;
